.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin-top : 80px;
    margin-bottom: 35px;
    display : flex;
    flex-direction: column;
    justify-content: space-evenly;
    background: orange;
    color : white;
    align-items: center;
    min-width : 40%;
    font-family: monospace;
    
}


.calo{
    border-radius: 15px;
    margin-top : 30px;
    margin-bottom: 30px;
    text-align: center;
    color : greenyellow;
}
.image{
    border-radius: 15px;
    margin-top : auto;
    margin-bottom: auto;
    display: block;
  margin-left: auto;
  margin-right: auto;    
}

.url{

    box-shadow: 0px 5px 10px rgb(71,71,71);
    color : black;
    text-align: center;
    font-family: monospace;
    min-width : 40%;
    font-size : 25px;
    border-radius: 10px;
    cursor: pointer;
    background-color:yellow;margin-left:auto;margin-right:auto;display:block;margin-top:10%;margin-bottom:10%;
    
}
.url:hover{
    background: orangered;
    color : white; 
}
.url:focus{
    outline: none;
}
#urlshare{
    text-decoration: none;
}