.App{
  min-height: 100vh;
  background-image: linear-gradient(180deg, #2af598 0%, #009efd 100%);
}

.search-form{
  min-height: 10vh;
  display : flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  margin-top : 20px; 
  border-radius: 10px;
  width : 35%;
  border : 0;
  padding : 10px;
  font-family: cursive;
  font-size: larger;
  text-transform: capitalize;
  color : grey;
  background: rgb(214, 212, 212); 

}

.search-button{
  background :  #009efa;
  border-radius: 10px;
  border : none;
  cursor: pointer;
  margin-top : 20px; 
  padding : 10px 20px;
  font-family: cursive;
  font-size: larger;
  text-transform: capitalize;
  color : grey; 
}
.recipes{
  display : flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0px 30px;
}
.title{
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 30px;
  text-decoration: underline;
  font-style: italic;
}
.search-bar:focus{
  outline: none;
  border-radius: 20px;
  background: honeydew;
}

.search-button:focus{
  outline: none;
  background: red;
  color : white;
}